.footer-top{
  padding: 3.125em 0;
  border-bottom: $border;
  &__inner{
    @include flex;
    justify-content: space-between;
  }
}

.footer-info{
  @include grid100gap(.75em);
  &__logo{
    width: 12.3125em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-contacts{
  display: flex;
  align-items: flex-start;
  &__item{
    text-align: right;
    @include grid100gap(.75em);
    @include lastM(0 3.125em 0 0);
  }
  &__content{
    display: flex;
  }
  .m-link{
    img{
      width: 1.25em;
      margin-right: .375em;
    }
  }
}

.footer-middle{
  padding: 3.125em 0;
  border-bottom: $border;
}

.footer-nav{
  &__show{
    display: flex;
  }
  &__title{
    padding-bottom: .375em;
    border-bottom: 1px solid rgba(15, 16, 21, 0.10);
  }
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1em);
    color: $gray;
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.footer-bottom{
  padding: 1.875em 0;
  &__inner{
    @include flex;
    justify-content: space-between;
  }
  &__links{
    @include flex;
  }
  &__link{
    @include lastM(0 2.5em 0 0);
    span{
      font-size: .9375em;
      line-height: 127%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer-top{
    padding: 1.875em 0;
    &__inner{
      display: block;
    }
  }

  .footer-info{
    gap: .5em;
    margin-bottom: 1.875em;
    &__logo{
      width: 10em;
    }
  }

  .footer-contacts{
    &__item{
      text-align: left;
      gap: .625em;
      @include lastM(0 2.5em 0 0);
      &._phone{
        margin-right: 2.5em;
        order: -1;
      }
    }
  }

  .footer-middle{
    padding: 2.5em 0;
    border-bottom: $border;
    &__inner{
      row-gap: 1.875em;
    }
  }

  .footer-nav{
    &__link{
      @include lastM(0 0 .75em);
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .footer-bottom {
    padding: 1.25em 0;
    &__inner {
      flex-wrap: wrap;
      justify-content: unset;
    }
    &__c{
      margin-right: 1.25em;
    }
    &__links{
      margin-top: 1em;
      width: 100%;
    }
    &__link{
      @include lastM(0 1.25em 0 0);
      span{
        font-size: .75em;
        line-height: 133%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer-contacts{
    flex-wrap: wrap;
    &__item{
      &:not(._phone){
        margin-right: 0;
      }
      &._phone{
        margin-right: 1.875em;
        order: -1;
      }
      &._socials{
        width: 100%;
        margin-top: 1.875em;
      }
    }
    .m-link{
      &__icon{
        display: none;
      }
    }
  }

  .footer-middle{
    padding: 1.875em 0;
  }

  .footer-nav{
    &._active{
      .footer-nav__btn img{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding-bottom: .375em;
      justify-content: space-between;
      border-bottom: 1px solid rgba(15, 16, 21, 0.10);
    }
    &__title{
      padding-bottom: 0;
      border-bottom: none;
    }
    &__btn{
      width: .75em;
      margin-left: 1em;
      flex-shrink: 0;
      @include center;
      img{
        transition: all .3s;
        width: 100%;
        display: block;
      }
    }
    &__hidden{
      margin-top: 1em;
      display: none;
    }
    &__link{
      span{
        font-size: .75em;
        line-height: 150%;
      }
    }
  }

  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: flex-start;
    }
    &__c{
      margin: 0 0 .75em;
    }
    &__links{
      margin-top: .75em;
    }
    &__link{
      @include lastM(0 1.25em 0 0);
      span{
        font-size: .625em;
        line-height: 120%;
      }
    }
  }
}
