.about{
  @media screen and (min-width: 993px){
    .info-block{
      grid-template-columns: 43.875em 27.25em;
    }
  }
  &__desc{
    margin-top: 3.125em;
  }
  &__grid{
    @include flex;
    justify-content: space-between;
    margin-top: 3.19em;
  }
  &__info{
    width: 33.5625em;
    border-radius: 1em;
    background-color: $bgGray;
    padding: 1.875em;
    display: flex;
    align-items: center;
    img{
      width: 3.75em;
      margin-right: 1.875em;
    }
  }
}

.nums{
  display: flex;
  align-items: flex-start;
}

.num-item{
  @include lastM(0 3.92em 0 0);
  @include grid100gap(.5em);
  &__title{
    background: $grad;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 5em;
    font-weight: 500;
    line-height: 104%;
    font-family: $font2;
  }
}

.clients{
  padding-bottom: 8.75em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
    gap: 1.875em;
    margin-top: 3.125em;
  }
}

.client-card{
  @include center;
  border-radius: 0.5625em;
  border: $border;
  height: 5em;
  padding: .94em;
  img{
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__desc{
      margin-top: 2.5em;
    }
    &__grid{
      display: block;
      margin-top: 2.5em;
    }
    &__info{
      padding: 1.25em;
      width: 100%;
      img{
        width: 2.6875em;
        margin-right: 1.25em;
      }
    }
  }

  .nums{
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3));
    }
  }

  .num-item{
    @include lastM(0);
    &__title{
      font-size: 4.375em;
    }
  }

  .clients{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      margin-top: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    .info-block{
      gap: 1em;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__grid{
      margin-top: 1.875em;
    }
    &__info{
      display: block;
      img{
        width: 2.125em;
        margin: 0 0 1em;
      }
    }
  }

  .nums{
    justify-content: space-between;
  }

  .num-item{
    gap: .125em;
    min-width: 4.22em;
    &__title{
      font-size: 2.625em;
    }
  }

  .clients{
    padding-bottom: 3.75em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      margin-top: 1.875em;
    }
  }
}
