.page-block{
  padding: 0 0 6.25em;
}

.pagination{
  margin-top: 3.125em;
  &__items{
    @include center;
  }
  &__item{
    width: 4.375em;
    height: 4.375em;
    margin: 0 .5em;
    flex-shrink: 0;
    border-radius: 50%;
    border: 1px solid $bgGray;
    @include center;
    &:hover,
    &._active{
      border-color: $primary;
      background-color: $primary;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    &._disabled{
      background: rgba(88, 94, 102, 0.20);
      pointer-events: none;
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .page-block{
    padding-bottom: 5em;
  }

  .cases-page{
    .h1{
      max-width: 11em;
    }
    .cases__desc{
      max-width: 28.5625em;
    }
  }

  .pagination{
    margin-top: 2.5em;
    &__item{
      width: 3.75em;
      height: 3.75em;
      svg{
        width: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .page-block{
    padding-bottom: 3.75em;
  }

  .pagination{
    margin-top: 1.875em;
    ._hide-mob{
      display: none;
    }
    &__item{
      width: 2.9375em;
      height: 2.9375em;
      margin: 0 .375em;
      svg{
        width: .625em;
      }
    }
  }
}
