.page-bg{
  background-repeat: no-repeat;
  background-position: right 0 top 2.5em;
  background-size: 58.875em auto;
}

.error-page{
  padding: 8.75em 0;
  background-image: url(../img/404-bg.svg);
  &__top{
    max-width: 52em;
  }
  &__grid{
    margin-top: 6.25em;
    @include flex;
    justify-content: space-between;
  }
  &__num{
    font-size: 13.75em;
    line-height: 100%;
    font-weight: 500;
    color: $primary;
    font-family: $font2;
  }
  &__text{
    max-width: 26.4375em;
  }
}


@media screen and (max-width: 992px){
  .page-bg{
    background-position: right 0 top 2.37em;
    background-size: 32.4375em auto;
  }

  .error-page{
    padding: 5em 0;
    background-image: url(../img/404-bg2.svg);
    &__top{
      max-width: unset;
      text-align: center;
    }
    &__grid{
      margin-top: 2.5em;
      flex-direction: column;
      text-align: center;
    }
    &__num{
      font-size: 15.375em;
      line-height: 78%;
    }
    &__text{
      margin-top: 1.875em;
      max-width: 29.125em;
    }
    &__link{
      margin-top: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .page-bg{
    background-position: right 0 top 8.625em;
    background-size: 17.875em auto;
  }

  .error-page{
    padding: 3.75em 0;
    background-image: url(../img/404-bg3.svg);
    &__grid{
      margin-top: 1.875em;
    }
    &__num{
      font-size: 8.75em;
    }
    &__text{
      margin-top: 1.25em;
    }
    &__link{
      margin-top: 1.875em;
    }
  }
}
