.contacts{
  padding: 8.75em 0;
  background-color: $primary;
  color: #fff;
  background-image: url(../img/contacts-bg2.svg),url(../img/contacts-bg.svg);
  background-repeat: no-repeat;
  background-position: left top 7.25em, left top;
  background-size: 48.25em auto ,100% auto;
  &__grid{
    position: relative;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  &__desc{
    max-width: 33em;
  }
  &__hint{
    position: absolute;
    right: 0;
    top: -12.125em;
    width: 10em;
    filter: drop-shadow(2px 7px 17px #0000001F)
            drop-shadow(22px 65px 41px #0000000F)
            drop-shadow(39px 115px 49px #00000005)
            drop-shadow(61px 180px 53px #00000000);
  }
}

.contacts-form{
  margin-top: 1.875em;
  &__files{
    margin-top: .625em;
  }
  &__bottom{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-right: 1.25em;
  }
  &__hint{
    opacity: 0.5;
    max-width: 20em;
  }
}

._full{
  grid-column: 1/-1;
}

.phone-field{
  position: relative;
  z-index: 2;
  &._black{
    .phone-field__toggle{
      border: 1px solid rgba(29, 78, 216, 0.10);
      background: rgba(29, 78, 216, 0.10);
    }
    .phone-field__arrow{
      path{
        stroke: $black;
      }
    }
  }
  &._active{
    .phone-field__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .phone-field__arrow{
      transform: rotate(-180deg);
    }
  }
  .fg{
    font-size: 1em;
    input{
      padding-left: 4em;
    }
    label{
      left: 9em;
    }
  }
  &__toggle{
    cursor: pointer;
    position: absolute;
    top: 1.75em;
    left: .25em;
    height: 1.375em;
    width: 2.75em;
    border-radius: 0.3125em;
    border: 1px solid rgba(244, 246, 249, 0.20);
    background: rgba(244, 246, 249, 0.10);
    padding: .25em;
    display: flex;
    align-items: center;
  }
  &__flag{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .25em;
  }
  &__code{
    font-size: .875em;
    font-weight: 500;
    margin-right: .5em;
  }
  &__country{
    font-size: .875em;
    color: $gray;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.14em;
      height: 1px;
      background-color: rgba($gray,.2);
      margin-right: .57em;
    }
  }
  &__arrow{
    margin-left: auto;
    width: .75em;
    transition: all .3s;
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .44em;
    width: 100%;
    padding-right: .25em;
    background-color: #fff;
    border-radius: .375em;
    color: $black;
  }
  &__items{
    padding: .56em .25em .56em 0;
    max-height: 13.125em;
    overflow-y: auto;
    @include scroll;
    overflow-x: hidden;
  }
}

.phone-country{
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: -1em;
    transform: translateY(-50%);
    pointer-events: none;
    width: calc(100% + 1.5em);
    height: 100%;
    background: rgba(244, 246, 249, 0.50);
    opacity: 0;
    transition: all .3s;
  }
  &:after{
    content: '';
    flex-shrink: 0;
    margin-left: auto;
    width: 1em;
    height: 1em;
    background-image: url(../img/check.svg);
    @include bgImgSize(100%);
    transition: all .3s;
    opacity: 0;
    z-index: 2;
  }
}

.phone-country{
  display: flex;
  align-items: center;
  padding: 0 1em;
  position: relative;
  height: 2em;
  &:hover{
    cursor: pointer;
    &:before{
      opacity: 1;
    }
  }
  &._active{
    &:after{
      opacity: 1;
    }
  }
  & * {
    position: relative;
    z-index: 2;
  }
  .phone-field__flag{
    width: 1.625em;
    border: 1px solid rgba(88, 94, 102, 0.20);
    border-radius: .25em;
    margin-right: .5em;
  }
}

.info-block{
  display: grid;
  grid-template-columns: 43.875em 26.375em;
  justify-content: space-between;
  align-items: flex-end;
  gap: 1.875em;
}

.proper{
  padding: 8.75em 0;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.proper-card{
  padding: 2.5em;
  border-right: 1px solid #F3F4F9;
  border-top: 1px solid #F3F4F9;
  position: relative;
  &:first-child{
    border-top-left-radius: .8125em;
  }
  &:last-child{
    border-bottom-right-radius: .8125em;
  }
  @media screen and (min-width: 993px){
    padding-left: 7.8125em;
    &:nth-last-child(1),
    &:nth-last-child(2),
    &:nth-last-child(3){
      border-bottom: 1px solid #F3F4F9;
    }
    &:nth-child(3n - 2){
      border-left: 1px solid #F3F4F9;
    }
    &:nth-child(3){
      border-top-right-radius: .8125em;
    }
    &:nth-last-child(3){
      border-bottom-left-radius: .8125em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    min-height: 4.375em;
  }
  &__icon{
    position: absolute;
    top: 2.5em;
    left: 2.5em;
    width: 4.375em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 5em 0;
    background-image: url(../img/contacts-bg3.svg);
    background-position: right top 2.375em;
    background-size: 39.8125em auto;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__info{
      gap: 1.25em;
      max-width: 33em;
    }
    &__hint{
      top: -9.375em;
      width: 8.75em;
    }
  }

  .contacts-form{
    margin-top: 1.25em;
    &__files{
      margin-top: 1.25em;
    }
    &__btn{
      margin-right: 1.19em;
    }
    &__hint{
      max-width: 17em;
    }
  }

  .phone-field{
    font-size: .9375em;
  }

  .info-block{
    grid-template-columns: 100%;
  }

  .proper{
    padding: 5em 0;
    &__top{
      max-width: 28.375em;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(2,50%);
    }
  }

  .proper-card{
    padding: 1.875em;
    @media screen and (min-width: 577px){
      &:nth-last-child(1),
      &:nth-last-child(2){
        border-bottom: 1px solid #F3F4F9;
      }
      &:nth-child(odd){
        border-left: 1px solid #F3F4F9;
      }
      &:nth-child(2){
        border-top-right-radius: .8125em;
      }
      &:nth-last-child(2){
        border-bottom-left-radius: .8125em;
      }
    }
    &__top{
      display: block;
      min-height: unset;
      margin-bottom: 1.25em;
    }
    &__icon{
      position: static;
      width: 3.75em;
      margin: 0 0 .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding: 3.75em 0;
    background-image: url(../img/contacts-bg4.svg);
    background-position: right top 5.3125em;
    background-size: 100% auto;
    &__grid{
      gap: 1.875em;
    }
    &__info{
      gap: 1em;
    }
    &__hint{
      top: -6.875em;
      width: 6.25em;
    }
  }

  .contacts-form{
    margin-top: 1em;
    grid-template-columns: 100%;
    gap: 1em;
    &__files{
      margin-top: .875em;
    }
    &__bottom{
      display: block;
    }
    &__btn{
      margin: 0 0 1em;
      width: 100%;
    }
    &__hint{
      max-width: 100%;
    }
  }

  .phone-field{
    font-size: .75em;
  }

  .info-block{
    gap: 1.25em;
  }

  .proper{
    padding: 3.75em 0;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: 100%;
    }
  }

  .proper-card{
    padding: 1.25em;
    border-left: 1px solid #F3F4F9;
    &:last-child{
      border-bottom: 1px solid #F3F4F9;
      border-bottom-left-radius: .8125em;
    }
    &:first-child{
      border-top-right-radius: .8125em;
    }
    &__top{
      display: flex;
      align-items: center;
      margin-bottom: .5em;
    }
    &__icon{
      width: 2.75em;
      margin: 0 .5em 0 0;
    }
  }
}
