.blog-page{
  .tabs{
    margin-top: 3.125em;
  }
  &__items{
    margin-top: 3.125em;
    @media screen and (min-width: 993px){
      display: block;
      column-count: 3;
      column-gap: 1.875em;
      .blog-card{
        display: inline-block;
        width: 100%;
        @include lastM(0 0 2.5em);
        &__img{
          padding: 0;
          img{
            position: static;
            height: auto;
          }
        }
      }
    }
  }
}

.blog-card{
  &__img{
    @include imgCover(80.3%);
    border-radius: 1em;
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(1em);
  }
}

.blog-item{
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__nav{
    flex-shrink: 0;
  }
  &__info{
    @include flex;
    margin-top: 1em;
  }
  &__tags{
    margin-left: 1em;
    padding-left: 1.375em;
    position: relative;
    font-weight: 500;
    &:before{
      content: '';
      position: absolute;
      width: .375em;
      height: .375em;
      left: 0;
      top: .56em;
      border-radius: 50%;
      background-color: $primary;
    }
  }
}

.back-link{
  display: flex;
  align-items: center;
  white-space: nowrap;
  color: $gray;
  font-weight: 500;
  svg{
    width: 1.5em;
    margin-right: .625em;
  }
  &:hover{
    color: $orange;
    path{
      stroke: $orange;
    }
  }
}

.article{
  margin-top: 3.125em;
}

.text{
  hr{
    display: block;
    opacity: 0;
    height: 1.875em;
  }
  b{
    font-weight: 700;
  }
  img{
    display: block;
    width: 100%;
    border-radius: 1.1875em;
  }
  ul,ol,
  img,
  p{
    @include lastM(0 0 1.25em);
  }
  h3{
    @include lastM(0 0 .55em);
  }
  h4{
    @include lastM(0 0 .71em);
  }
  h5{
    @include lastM(0 0 .83em);
  }
  small{
    display: block;
    @include lastM(0 0 1em);
  }
  ol,
  ul{
    padding-left: 2.5em;
    @include grid100gap(.75em);
  }
  li{
    position: relative;
  }
  ul{
    li{
      &:before{
        content: '';
        position: absolute;
        right: 100%;
        margin-right: .5em;
        top: .25em;
        width: 1em;
        height: 1.03em;
        background-image: url(../img/li-marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
  ol{
    counter-reset: list;
    li{
      counter-increment: list;
      &:before{
        content: counter(list);
        position: absolute;
        top: .18em;
        right: 100%;
        margin-right: .72em;
        width: 1.81em;
        height: 1.81em;
        @include center;
        border-radius: 50%;
        border: 2px solid $primary;
        font-size: 0.6875em;
        font-weight: 800;
        color: $primary;
      }
    }
    b{
      font-weight: 800;
    }
  }
  blockquote{
    padding: 2em;
    background-color: $bgGray;
    @include lastM(0 0 1em);
    font-weight: 400;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: .4em;
      height: 100%;
      background-color: $primary;
    }
  }
}

.blog{
  padding: 6.25em 0;
  overflow: hidden;
  &__top{
    padding-right: 10em;
    min-height: 4.375em;
    display: flex;
    align-items: flex-end;
  }
}

.blog-slider{
  position: relative;
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    .tabs{
      margin-top: 1.875em;
    }
    &__items{
      margin-top: 2.5em;
      row-gap: 1.875em;
    }
  }

  .blog-item {
    &__top {
      display: block;
      position: relative;
    }
    &__nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 1.25em;
    }
    &__tags{
      &:before{
        top: .44em;
      }
    }
  }

  .back-link{
    svg{
      width: 1em;
      margin-right: .5em;
    }
  }

  .article{
    margin-top: 2.5em;
  }

  .text{
    hr{
      height: 1.25em;
    }
    img{
      border-radius: .625em;
    }
    p{
      @include lastM(0 0 1.43em);
    }
    h3{
      @include lastM(0 0 .67em);
    }
    h4{
      @include lastM(0 0 .83em);
    }
    h5{
      @include lastM(0 0 .91em);
    }
    ul{
      li{
        &:before{
          margin-right: .57em;
          top: .142em;
          width: 1.14em;
          height: 1.16em;
        }
      }
    }
    ol{
      li{
        &:before{
          font-size: 0.785em;
          top: -.1em;
        }
      }
    }
    blockquote{
      padding: 1.67em;
      @include lastM(0 0 1.11em);
      &:before{
        width: .44em;
      }
    }
  }

  .blog{
    padding: 3.125em 0;
    &__top{
      padding-right: 9em;
      min-height: 3.75em;
    }
  }

  .blog-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: 50%;
      }
      &-nav{
        margin-bottom: 1.875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog-page{
    .info-block{
      gap: 1em;
    }
    .tabs{
      margin-top: 1.25em;
    }
    &__items{
      margin-top: 1.875em;
      row-gap: 1.25em;
    }
  }

  .blog-card{
    &__content{
      margin-top: .75em;
      gap: .5em;
    }
  }

  .blog-item{
    &__nav{
      display: none;
    }
    &__info{
      display: block;
    }
    &__tags{
      margin: .5em 0 0;
      padding-left: .875em;
      &:before{
        top: .375em;
      }
    }
  }

  .article{
    margin-top: 1.875em;
  }

  .text{
    hr{
      height: .5em;
    }
    img{
      border-radius: .25em;
    }
    p{
      @include lastM(0 0 1.67em);
    }
    h3{
      @include lastM(0 0 1em);
    }
    h4{
      @include lastM(0 0 .94em);
    }
    h5{
      @include lastM(0 0 1.25em);
    }
    ul,ol{
      padding-left: 1.5em;
    }
    ul{
      gap: .5em;
      li{
        &:before{
          margin-right: .665em;
          top: .166em;
          width: 1.33em;
          height: 1.35em;
        }
      }
    }
    ol{
      li{
        &:before{
          font-size: 0.9166em;
        }
      }
    }
    blockquote{
      padding: 1.43em;
      @include lastM(0 0 1.43em);
      &:before{
        width: .57em;
      }
    }
  }

  .blog{
    padding: 1.875em 0;
    &__top{
      padding-right: 7em;
      min-height: 2.9375em;
    }
  }

  .blog-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
