.text h3,
.text h4,
.h1,
.h2,
.h3,
.h4{
  font-family: $font2;
  font-weight: 500;
  span{
    color: $primary;
  }
}

.h1{
  font-size: 4.375em;
  line-height: 114%;
}

.h2{
  font-size: 3em;
  line-height: 112%;
}

.text h3,
.h3{
  font-size: 2.25em;
  line-height: 122%;
}

.text h4,
.h4{
  font-size: 1.75em;
  line-height: 121%;
}

.text h5,
.h5{
  font-family: $font2;
  font-size: 1.5em;
  line-height: 117%;
}

.text blockquote,
.h6{
  font-family: $font2;
  font-size: 1.25em;
  line-height: 130%;
}

.text li,
.text p,
.p{
  font-size: 1em;
  line-height: 150%;
}

.text small,
.comm{
  font-size: .875em;
  line-height: 129%;
}

.text small{
  color: $gray;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.blue-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.gray-text2{
  color: rgba(#fff,.5);
}


@media screen and (max-width: 992px){
  .h1{
    font-size: 3.5em;
    line-height: 118%;
  }

  .h2{
    font-size: 2.25em;
    line-height: 122%;
  }

  .text h3,
  .h3{
    font-size: 1.875em;
    line-height: 127%;
  }

  .text h4,
  .h4-tablet,
  .h4{
    font-size: 1.5em;
    line-height: 117%;
  }

  .text h5,
  .h5{
    font-size: 1.375em;
    line-height: 118%;
  }

  .text blockquote,
  .h6{
    font-size: 1.125em;
    line-height: 133%;
  }

  .text li,
  .text p,
  .p{
    font-size: .875em;
    line-height: 143%;
  }

  .text small,
  .comm{
    font-size: .75em;
    line-height: 133%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
    line-height: 143%;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
    line-height: 150%;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px){
  .h1{
    font-size: 1.625em;
    line-height: 123%;
  }

  .h2{
    font-size: 1.375em;
    line-height: 118%;
  }

  .text h3,
  .h3{
    font-size: 1.25em;
    line-height: 120%;
  }

  .text h4,
  .h4{
    font-size: 1.125em;
    line-height: 122%;
  }

  .text h5,
  .h5{
    font-size: 1em;
    line-height: 125%;
  }

  .text blockquote,
  .h6-mob,
  .h6{
    font-size: .875em;
    line-height: 128%;
  }

  .text li,
  .text p,
  .p{
    font-size: .75em;
    line-height: 150%;
  }

  .text small,
  .comm{
    font-size: .625em;
    line-height: 120%;
  }

  .text12-mob{
    font-size: .75em;
    line-height: 150%;
  }
}
