.marketer{
  padding: 8.75em 0 0;
  overflow: hidden;
  background-color: $bgGray;
  position: relative;
  &__info{
    width: 41em;
    padding: 7.69em 0 8.75em;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__icon{
    width: 5.625em;
    display: block;
    margin-bottom: 2.5em;
    position: relative;
    z-index: 2;
  }
  .h2{
    position: relative;
    z-index: 2;
  }
  &__round{
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 120em;
    img{
      width: 100%;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  @media screen and (min-width: 993px){
    .marketer-item{
      position: absolute;
      z-index: 3;
      max-width: 15.8125em;
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(5){
        flex-direction: row-reverse;
        text-align: right;
        .marketer-item__icon{
          margin: 0 0 0 .75em;
        }
      }
      &:nth-child(1){
        left: 50%;
        margin-left: -24em;
        top: 9.13em;
      }
      &:nth-child(2){
        right: 50%;
        margin-right: -24em;
        top: 9.13em;
      }
      &:nth-child(3){
        left: 50%;
        margin-left: -35em;
        top: 17.13em;
        max-width: 13.8125em;
      }
      &:nth-child(4){
        right: 50%;
        margin-right: -36em;
        top: 17.13em;
      }
      &:nth-child(5){
        left: 50%;
        margin-left: -38em;
        top: 24.13em;
        max-width: 12.8125em;
      }
      &:nth-child(6){
        right: 50%;
        margin-right: -41em;
        top: 24.13em;
      }
    }
  }
}

.marketer-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 2.6875em;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    font-size: .875em;
    line-height: 143%;
  }
}

.consult{
  @media screen and (min-width: 993px){
    background-image: url(../img/consult-bg.svg);
    background-size: 108.1875em auto;
    background-position: right top 3.75em;
  }
  &__grid{
    position: relative;
  }
  .contacts-form{
    max-width: 61.75em;
  }
}


@media screen and (max-width: 992px){
  .marketer{
    padding: 2.5em 0 4.81em;
    &__info{
      width: 35.625em;
      padding: 5em 0 0;
    }
    &__icon{
      margin-bottom: 1.875em;
      width: 5em;
    }
    &__round{
      width: 100%;
      top: 2.5em;
      bottom: unset;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__items{
      margin-top: 3.125em;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.875em 1.25em;
      position: relative;
      z-index: 2;
    }
  }
}


@media screen and (max-width: 576px){
  .marketer{
    padding-bottom: 3.75em;
    &__info{
      width: 16.5em;
      padding-top: 2.5em;
    }
    &__icon{
      margin-bottom: 1.25em;
      width: 3.75em;
    }
    &__round{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__items{
      margin-top: 1.875em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .marketer-item{
    &__text{
      font-size: .75em;
      line-height: 150%;
    }
  }
}
