.who{
  padding: 8.75em 0;
  background-color: $black;
  color: #fff;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    background-image: url(../img/who-bg.svg);
    background-size: 40.625em auto;
    background-repeat: no-repeat;
    background-position: right 0 top 5.94em;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    position: relative;
  }
  &__top{
    max-width: 65em;;
  }
  &__nav{
    display: flex;
    margin-top: 3.125em;
    .m-btn{
      margin-right: 1.87em;
    }
  }
  &__bottom{
    margin-top: 6.25em;
    padding-top: 3.125em;
    border-top: 1px solid rgba(255, 255, 255, 0.20);
    display: grid;
    grid-template-columns: 35.125em 36.4375em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__bg{
    position: absolute;
    right: 0;
    top: 5.94em;
    width: 40.625em;
  }
  &__link{
    position: absolute;
    right: 0;
    bottom: -12.5em;
    padding: 1.25em;
    width: 12.3125em;
    border-radius: 1em;
    &-top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1em;
      img{
        width: .6875em;
      }
    }
    &-text{
      color: $blueLite;
    }
  }
}

.advantages{
  padding: 6.25em 0;
  background-color: $bgGray;
}

.advantage-card{
  padding: 1.25em;
  border-radius: 1.1875em;
  background-color: #fff;
  &__top{
    margin-bottom: 1em;
  }
  &__icon{
    height: 2.75em;
    margin-bottom: .75em;
    img{
      height: 100%;
      display: block;
      width: auto;
    }
  }
}

.team{
  padding: 8.75em 0;
  overflow: hidden;
  &__top{
    max-width: 55em;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
    }
  }
}

.swiper{
  &-nav{
    display: flex;
  }
  &-button{
    width: 4.375em;
    height: 4.375em;
    border-radius: 50%;
    border: 1px solid $bgGray;
    @include center;
    transition: all .125s;
    svg{
      width: 1em;
    }
    &:hover{
      background-color: $primary;
      border-color: $primary;
      path{
        stroke: #fff;
      }
    }
    &.swiper-button-disabled{
      opacity: 1;
      background: rgba(88, 94, 102, 0.20);
      border-color: transparent;
      path{
        stroke: #fff;
      }
    }
  }
  &-pagination{
    position: static;
    margin-top: 2.5em;
    @include center;
    &-bullet{
      width: 0.375em;
      height: 0.375em;
      opacity: .4;
      background-color: $gray;
      margin: 0 .9375em !important;
      &:hover{
        opacity: 1;
      }
      &-active{
        width: 0.625em;
        height: 0.625em;
        background-color: $black;
        opacity: 1;
      }
    }
  }
}

._float-nav{
  .swiper{
    &-nav{
      position: absolute;
      bottom: 100%;
      margin-bottom: 3.125em;
      right: 0;
    }
    &-button{
      position: static;
      margin: 0 0 0 1.25em;
      transform: unset;
    }
  }
}

.team-slider{
  margin-top: 3.125em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.team-card{
  &:hover{
    .team-card__img {
      ._view2{
        opacity: 1;
      }
      ._view1{
        opacity: 0;
      }
    }
  }
  &__img{
    border-radius: 0.47488em;
    @include imgCover(140%);
    img{
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
}


@media screen and (max-width: 992px){
  .who{
    padding: 5em 0;
    &:before{
      background-image: url(../img/who-bg2.svg);
      background-size: 23.125em auto;
      background-position: right 0 top 14em;
    }
    &__nav{
      margin-top: 2.5em;
      .m-btn{
        margin-right: 1.19em;
      }
    }
    &__bottom{
      margin-top: 3.75em;
      padding-top: 2.5em;
      grid-template-columns: 32em;
      .p{
        padding-right: 3em;
      }
    }
    &__link{
      width: 16.0625em;
      bottom: -8.0625em;
      &-top{
        margin-bottom: .5em;
      }
    }
  }

  .advantages{
    padding: 5em 0;
  }

  .team{
    padding: 5em 0;
    &__top{
      max-width: 34em;
    }
  }

  .slider{
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-pagination{
        margin-top: 1.875em;
      }
    }
  }

  ._float-nav{
    .swiper{
      &-nav{
        margin-bottom: 2.5em;
      }
    }
  }

  .swiper{
    &-button{
      width: 3.75em;
      height: 3.75em;
      margin-left: 1em;
      svg{
        width: .875em;
      }
    }
  }

  .team-slider{
    margin-top: 2.5em;
    .swiper{
      &-slide{
        width: 20.625em;
      }
    }
  }

  .team-card{
    @media screen and (min-width: 577px){
      .p{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .who{
    padding: 3.75em 0 4.38em;
    &:before{
      background-image: url(../img/who-bg3.svg);
      background-size: 17.375em auto;
      background-position: right 0 top 8em;
    }
    &__nav{
      margin-top: 1.875em;
      @include grid100gap(1.25em);
      .m-btn{
        margin: 0;
      }
    }
    &__bottom{
      margin-top: 1.875em;
      padding-top: 1.25em;
      grid-template-columns: 100%;
      .p{
        padding-right: 0;
      }
    }
    &__link{
      width: 100%;
      bottom: -7em;
    }
  }

  .advantages{
    padding: 3.75em 0;
  }

  .advantage-card{
    &__top{
      display: flex;
      align-items: center;
    }
    &__icon{
      width: 2.75em;
      flex-shrink: 0;
      margin: 0 .75em 0 0;
      img{
        width: 100%;
        height: auto;
      }
    }
    .h6{
      font-size: 1.125em;
      line-height: 133%;
    }
    .p{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .team{
    padding: 3.75em 0;
  }

  .swiper{
    &-pagination{
      &-bullet{
        margin: 0 .625em !important;
      }
    }
    &-button{
      width: 2.9375em;
      height: 2.9375em;
      svg{
        width: .625em;
      }
    }
  }

  ._float-nav{
    .swiper{
      &-button{
        margin-left: .75em;
      }
    }
  }

  .team-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width:17.5em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .team-card{
    &__img{
      border-radius: 0.39831em;
    }
  }
}
