.modal {
    height: $vh;
    overflow-y: auto;
    background-color: $bgGray;
    background-image: url(../img/modal-bg.svg);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: left bottom;
    z-index: 50;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding-bottom: 3.125em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &__top{
        padding: 3.125em 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__logo{
        width: 12.25em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__close{
        @include flex;
        svg{
            margin-left: .625em;
            width: 2.75em;
        }
        &:hover{
            color: $primary;
            path{
                stroke: $primary;
            }
            g{
                opacity: 1;
            }
        }
    }
}

.modal-info{
    margin-top: 10em;
    @include grid100gap(1.875em);
    max-width: 47em;
}

.modal-form{
    width: 61.1875em;
    max-width: 100%;
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    gap: 3.125em;
    &__bottom{
        @include flex;
    }
    &__btn{
        margin-right: 1.25em;
    }
    &__hint{
        opacity: .5;
        max-width: 23em;
    }
}

.modal-socials{
    margin-top: 3.125em;
    @include grid100gap(.75em);
}

.vacancy-modal{
    .modal-form{
        width: 100%;
        grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
        gap: 2.5em;
    }
    @media screen and (min-width: 993px){
        ._full2{
            grid-column: span 2;
        }
    }
}

.modal-thank{
    margin-top: 10em;
    .modal-info{
        margin: 0;
        &__desc{
            max-width: 32em;
        }
    }
    &__nav{
        display: flex;
        margin-top: 3.125em;
    }
    &__icon{
        width: 5em;
        display: block;
        margin-bottom: 1.875em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        background-image: url(../img/modal-bg2.svg);
        padding-bottom: 1.75em;
        &__top{
            padding: 1.75em 0;
        }
        &__logo{
            width: 10em;
        }
        &__close{
            svg{
                width: 1.5em;
            }
        }
    }

    .modal-info{
        gap: 1.25em;
        &__desc{
            max-width: 28em;
        }
    }

    .modal-form{
        margin-top: 2.5em;
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 2.5em 1.25em;
        &__bottom{
            display: grid;
            grid-template-columns: repeat(2,calc(50% - .625em));
            gap: 2.5em 1.25em;
        }
        &__btn{
            margin: 0;
            width: 100%;
        }
        &__hint{
            max-width: 100%;
        }
    }

    .modal-socials{
        margin-top: 2.5em;
        gap: 1em;
    }

    .vacancy-modal{
        .modal-form{
            grid-template-columns: 100%;
            gap: 1.31em;
        }
    }

    .modal-thank{
        text-align: center;
        .modal-info{
            &__desc{
                margin: 0 auto;
                width: 29em;
                max-width: 100%;
            }
        }
        &__nav{
            justify-content: center;
            margin-top: 2.5em;
        }
        .modal-form__btn{
            width: auto;
        }
        &__icon{
            width: 3.75em;
            margin: 0 auto 1.875em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        background-image: url(../img/modal-bg3.svg);
        padding-bottom: 1em;
        &__top{
            padding: 1em 0;
        }
        &__logo{
            width: 6.9em;
        }
        &__close{
            span{
                display: none;
            }
        }
    }

    .modal-info{
        gap: 1em;
        margin-top: .875em;
    }

    .modal-form{
        margin-top: 1.25em;
        grid-template-columns: 100%;
        gap: 1em;
        &__bottom{
            grid-template-columns: 100%;
            gap: 1em;
            margin-top: .875em;
        }
    }

    .modal-socials{
        margin-top: 1.875em;
    }

    .vacancy-modal{
        .modal-form{
            gap: 1em;
        }
    }

    .modal-thank{
        margin-top: 4.75em;
        &__nav{
            margin-top: 1.875em;
        }
        .modal-form__btn{
            width: 100%;
        }
        &__icon{
            width: 2.75em;
            margin-bottom: 1.25em;
        }
    }
}
