.service-detail{
  padding: 8.75em 0;
  &__top{
    max-width: 44em;
  }
  &__items{
    margin-top: 3.125em;
  }
}

.service-card3{
  &__icon{
    width: 4.375em;
    height: 4.375em;
    @include center;
    border-radius: 50%;
    border: $border;
    margin-bottom: 1.25em;
    img{
      max-width: 2.5em;
      max-height: 2.5em;
    }
  }
  .h6{
    max-width: 12.5em;
  }
  &__info{
    margin-top: 1em;
  }
}

.calculate{
  padding: 8.75em 0;
  background-color: $bgGray;
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 40em;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 2.81em;
  }
  &__img{
    position: absolute;
    top: -19em;
    width: 63.75em;
    right: -6.875em;
    pointer-events: none;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}

.calculate-socials{
  margin-left: 1.875em;
  @include grid100gap(.25em);
}

.example{
  position: absolute;
  padding: 1.875em;
  background-image: url(../img/cart.svg), $grad;
  background-repeat: no-repeat;
  background-size: 6.8125em auto, cover;
  background-position: right bottom;
  border-radius: 1.125em;
  box-shadow: 0 .75em 1.875em rgba(8, 49, 111, 0.24);
  width: 17.0625em;
  top: 50%;
  transform: translateY(-60%);
  right: 0;
  z-index: 3;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    top: 1em;
    left: 1em;
    width: 100%;
    height: 100%;
    border-radius: 1.5em;
    background: rgba(29, 78, 216, 0.10);
    backdrop-filter: blur(3px);
    z-index: -1;
  }
  &__logo{
    width: 6.5em;
    display: block;
  }
  &__info{
    margin-top: 1.3em;
    @include grid100gap(.75em);
  }
  &__btn{
    margin-top: 1.875em;
    width: 100%;
    padding: 0;
  }
}

.stages{
  padding: 8.125em 0;
  background-color: $black;
  color: #fff;
  @media screen and (min-width: 993px){
    .info-block{
      grid-template-columns: 43.875em 26.4375em;
    }
  }
  &__img{
    margin-top: 3.125em;
    width: calc(100% + 18.75em);
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .service-detail{
    padding: 5em 0;
    &__items{
      margin-top: 2.5em;
      grid-template-columns: repeat(2,calc(50% - .9375em));
    }
  }

  .service-card3{
    &__top{
      display: flex;
      align-items: center;
    }
    &__icon{
      width: 3.75em;
      height: 3.75em;
      margin: 0 1em 0 0;
      flex-shrink: 0;
      img{
        max-width: 2.125em;
        max-height: 2.125em;
      }
    }
  }

  .calculate{
    padding: 5em 0 0;
    overflow: hidden;
    &__info{
      gap: 1.25em;
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__img{
      position: static;
      width: 60em;
      margin-top: 1.625em;
      margin-left: -13em;
      img{
        width: 100%;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .calculate-socials{
    margin-left: 1.25em;
    gap: .5em;
  }

  .example{
    padding: 1.25em;
    top: unset;
    bottom: 26em;
    transform: unset;
    &__logo{
      width: 6.5em;
      display: block;
    }
    &__info{
      margin-top: 1em;
      gap: .5em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }

  .stages{
    padding: 5em 0;
    overflow: hidden;
    .info-block{
      gap: 1.25em;
      max-width: 29em;
    }
    &__img{
      margin-top: 2.5em;
      width: 100%;
      img{
        height: 25em;
        width: auto;
      }
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .service-detail{
    padding: 3.75em 0;
    &__items{
      margin-top: 1.875em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .service-card3{
    &__icon{
      width: 2.9375em;
      height: 2.9375em;
      flex-shrink: 0;
      img{
        max-width: 1.6875em;
        max-height: 1.6875em;
      }
    }
    &__info{
      margin-top: .75em;
    }
  }

  .calculate{
    padding: 3.75em 0 0;
    &__info{
      gap: 1em;
    }
    &__nav{
      margin-top: 1.875em;
      display: block;
    }
    &__btn{
      width: 100%;
    }
    &__img{
      margin-top: 1.875em;
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .calculate-socials{
    margin: 1em 0 0;
    text-align: center;
    .socials{
      justify-content: center;
    }
  }

  .example{
    padding: 1em;
    width: 12.3125em;
    bottom: 10.19em;
    display: flex;
    align-items: center;
    background-size: 4.4375em auto, cover;
    &:before{
      top: .625em;
      left: .625em;
    }
    &__logo{
      display: none;
    }
    &__info{
      margin-top: 0;
    }
    &__btn{
      width: 2.9375em;
      height: 2.9375em;
      border-radius: 50%;
      padding: 0;
      flex-shrink: 0;
      margin: 0 0 0 .5em;
      span{
        display: none;
      }
      svg{
        margin: 0;
      }
    }
  }

  .stages{
    padding: 3.75em 0;
    .info-block{
      gap: 1em;
    }
    &__img{
      margin-top: 1.875em;
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      padding: 0 .75em;
      overflow-x: auto;
      img{
        height: 16.75em;
      }
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
  }
}
