.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    font-weight: 300;
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba(88, 94, 102, 0.20);
    padding: 1.75em .625em 1em;
    height: 4em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: transparent;
    color: $black;
    &:focus{
      border-color: $black;
    }
    &._filled,
    &:focus{
      &~label{
        top: 0;
      }
    }
  }
  textarea{
    height: 7em;
    resize: none;
  }
  label{
    font-weight: 300;
    font-size: 1em;
    line-height: 150%;
    position: absolute;
    top: 1.37em;
    left: .625em;
    transition: all .2s;
    pointer-events: none;
    color: rgba($black,.5);
  }
  &._static{
    display: flex;
    label{
      order: -1;
      position: static;
      margin-right: .75em;
      height: 4em;
      padding-bottom: 1em;
      display: flex;
      align-items: flex-end;
      flex-shrink: 0;
      color: inherit;
    }
    textarea{
      resize: vertical;
    }
  }
}

.m-files,
.phone-item{
  display: flex;
  .m-files__title{
    margin-right: 1em;
  }
  .file-input__title{
    white-space: nowrap;
  }
}

.m-files__title{
  font-size: 1em;
  line-height: 150%;
}

.m-label{
  margin-right: .75em;
  height: 4em;
  padding-bottom: 1em;
  display: flex;
  align-items: flex-end;
  flex-shrink: 0;
  color: inherit;
}

.fg-white{
  textarea,
  input{
    border-color: rgba(255, 255, 255, 0.20);
    color: #fff;
    &:focus{
      border-color: #fff;
    }
    &::placeholder{
      color: rgba(#fff,.5);
    }
  }
  label{
    color: rgba(#fff,.5);
  }
}

.custom-file-container{
  height: 100%;
  width: 100%;
  min-height: 100%;
  position: static;
  .label-container{
    display: none;
  }
  .input-container{
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    //z-index: 3;
    input{
      min-width: unset;
      height: 100% !important;
    }
  }
  .image-preview{
    overflow: unset;
    background-color: transparent;
    background-image: unset !important;
    padding: 0;
    height: auto;
    width: auto;
    max-width: calc(100% - 2em);
    margin: 0;
    transform: translateY(-.75em);
    display: inline-flex;
    flex-wrap: wrap;
    &:after{
      content: '';
      flex-shrink: 0;
      position: absolute;
      left: 100%;
      bottom: 0;
      background-image: url(../img/plus.svg);
      @include bgImgSize(100%);
      align-self: center;
      margin-top: .75em;
      opacity: 0;
      width: .9375em;
      height: 3.6875em;
      pointer-events: none;
      margin-left: 1em;
      z-index: -1;
    }
  }
  .input-visible{
    opacity: 0;
    pointer-events: none;
  }
  .image-preview-item{
    width: 4.3125em;
    height: 3.6875em;
    border-radius: 0.5em;
    background-size: 2.5em auto;
    background-repeat: no-repeat;
    background-position: center top;
    margin: .75em .75em 0 0;
    box-shadow: none;
    float: unset;
    z-index: 4;
    &-clear{
      z-index: 4;
      width: 100%;
      left: 50%;
      bottom: 0;
      top: unset;
      transform: translateX(-50%);
      background-color: transparent;
      border-radius: 50%;
      height: 1em;
      @include center;
      box-shadow: unset;
      &:after{
        content: 'удалить';
        font-size: .875em;
      }
      &:hover{
        background-color: transparent;
      }
      &-icon{
        margin: 0 .13em 0 0;
        width: .8125em;
        height: .8125em;
        background-image: url(../img/close.svg);
        @include bgImgSize(100%);
        color: transparent;
        position: static;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}

.file-input{
  font-weight: 300;
  &:not(._white){
    .image-preview-item{
      &:after{
        content: '';
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 2.5em;
        height: 2.5em;
        border: 1px solid rgba(88, 94, 102, 0.20);
        border-radius: 0.5em;
      }


    }
  }
  &._white{
    .file-input__title{
      color: #fff;
      border-color: #fff;
    }
    .file-input__btn{
      path{
        stroke: #fff;
      }
    }
    .image-preview{
      &:after{
        background-image: url(../img/plus2.svg);
      }
      &-item{
        &-clear{
          &-icon{
            background-image: url(../img/close2.svg);
          }
        }
      }
    }
  }
  input{
    //opacity: 1;
  }
  &._uploaded{
    .file-input__btn{
      height: 3.6875em;
    }
    .file-input__title{
      opacity: 0;
    }
    .file-input__upload{
      opacity: 1;
    }
    .image-preview:after{
      opacity: 1;
    }
  }
  &__wrap{
    position: relative;
    min-height: 3.0625em;
    padding-left: 3.5em;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    pointer-events: none;
  }
  &__btn{
    width: 1.5em;
    height: 1.5em;
    @include center;
    border-radius: 0.5625em;
    margin-right: .5em;
    svg{
      width: 1.5em;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 128%;
    color: $black;
    border-bottom: 1px dashed $black;
  }
}


@media screen and (max-width: 992px){
  .phone-item__title,
  .m-files__title,
  .fg{
    font-size: .9375em;
  }

  .file-input{
    font-size: .86em;
  }
}


@media screen and (max-width: 576px){
  .phone-item__title,
  .m-files__title,
  .fg{
    font-size: .75em;
  }

  .file-input{
    font-size: .71em;
  }
}
