.header{
  &__logo{
    display: block;
    width: 12.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top,
.header-bottom{
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header-top{
  padding: .8125em 0;
  border-bottom: $border;
}

.nav,
.nav2{
  ul{
    @include flex;
  }
  .menu-item{
    @include lastM(0 1.875em 0 0);
    a{
      font-size: 0.875em;
      line-height: 129%;
      &:hover{
        color: $primary;
      }
    }
  }
}

.nav{
  .menu-item{
    a{
      color: $gray;
    }
  }
}

.nav2{
  .menu-item-has-children{
    position: relative;
    &:hover{
      ul{
        opacity: 1;
        pointer-events: unset;
        &:before{
          pointer-events: unset;
        }
      }
    }
    ul{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      border-radius: 0.3125em;
      border: $border;
      background: #FFF;
      box-shadow: 0 .25em 1.3125em rgba(0, 0, 0, 0.05);
      z-index: 5;
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 100%;
      margin-top: 1.25em;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2em;
        bottom: 100%;
        margin-bottom: -2px;
        opacity: 0;
        pointer-events: none;
      }
      li{
        height: 3.375em;
      }
      a{
        display: flex;
        align-items: center;
        height: 100%;
        font-size: .875em;
        line-height: 128%;
        padding: 0 1.43em;
        white-space: nowrap;
        &:hover{
          color: $primary;
          background-color: $bgGray;
        }
      }
    }
  }
}

.header-contacts{
  @include flex;
  .socials{
    &__item{
      @include lastM(0 1.875em 0 0);
      img{
        width: 1.25em;
      }
    }
  }
  &__phone{
    display: none;
  }
  &__email{
    margin-left: 1.875em;
    img{
      width: 1.25em;
      margin-right: .375em;
    }
  }
}

.header-bottom{
  padding: 1.875em 0;
}

.header-action{
  @include flex;
}

.burger{
  display: none;
}

.header-contacts2{
  margin-right: 1em;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__time{
    margin-top: .375em;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 0.375em;
      height: 0.375em;
      background-color: $green;
      border-radius: 50%;
      margin-right: .375em;
    }
  }
}

.nav2{
  font-weight: 500;
  .menu-item-has-children{
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: .75em;
      height: .75em;
      flex-shrink: 0;
      background-image: url(../img/arrow-bottom.svg);
      @include bgImgSize(100%);
      margin-left: .375em;
      cursor: pointer;
    }
  }
}

.menu{
  display: none;
}

.m-link{
  @include flex;
  font-weight: 500;
  color: $primary;
  span{
    transition: inherit;
    border-bottom: 1px solid rgba(29, 78, 216, 0.50);
    border-top: 1px solid transparent;
  }
  &__icon{
    @include center;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background-color: $bgGray;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      width: 1em;
    }
  }
  &:hover{
    span{
      color: $orange;
      border-bottom-color: rgba($orange,.6);
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 10em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__dialog{
          transform: translateX(0);
        }
      }
    }
  }

  .nav{
    .menu-item{
      @include lastM(0 1em 0 0);
      a{
        font-size: 0.75em;
        line-height: 133%;
      }
    }
  }

  .header-contacts{
    .socials{
      &__item{
        @include lastM(0 1.25em 0 0);
        img{
          margin-right: .375em;
        }
      }
    }
    &__email{
      margin-left: 1.25em;
      img{
        width: 1em;
      }
    }
  }

  .header-bottom{
    padding: 1em 0;
  }

  .burger{
    @include center;
    width: 1.625em;
    margin-left: 1.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }

  .header-contacts2{
    &__time{
      margin-top: .25em;
    }
  }

  .nav2{
    display: none;
  }

  .menu{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    overflow: hidden;
    z-index: 30;
    opacity: 0;
    pointer-events: none;
    &__bg{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba($black, 0.80);
    }
    &__dialog{
      position: absolute;
      top: 0;
      right: 0;
      z-index: 3;
      height: $vh;
      width: 31em;
      max-width: 100%;
      background-color: #fff;
      transition: all .5s;
      transform: translateX(120%);
    }
    &__top{
      @include flex;
      justify-content: space-between;
      padding: 0 1.75em 0 2.5em;
      height: 4.5em;
      border-bottom: $border;
    }
    &__close{
      width: 1.0625em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
    &__bottom{
      height: 11.625em;
      padding: 2.5em;
      background-color: $bgGray;
    }
    &__scroll{
      overflow-y: auto;
      height: calc(100 * var(--vh) - 16.125em);
      padding: 1.875em 2.5em;
    }
  }

  .menu-contacts{
    @include grid100gap(.625em);
  }

  .menu-contacts2{
    margin-top: 1.875em;
    @include flex;
    .socials{
      &__item{
        @include lastM(0 1em 0 0);
        color: $black;
      }
    }
    &__email{
      margin-left: 2.5em;
    }
  }

  .menu-nav{
    @include grid100gap(1.875em);
    &__item{
      display: flex;
      align-items: flex-start;
    }
    &__icon{
      width: 1.875em;
      flex-shrink: 0;
      margin-right: .75em;
    }
    &__content{
      min-height: 1.875em;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    &__title{
      font-family: $font2;
      font-size: 1.125em;
      line-height: 133%;
    }
    &__list{
      margin-top: .94em;
      @include grid100gap(.75em);
      a{
        font-size: .875em;
        line-height: 143%;
        color: rgba($black,.5);
        &:hover{
          color: $primary;
        }
      }
    }
  }

  .menu-nav2{
    margin-top: 1.875em;
    padding-top: 1.875em;
    border-top: $border;
    ul{
      @include grid100gap(1em);
    }
    a{
      font-size: .875em;
      line-height: 143%;
      color: $gray;
      &:hover{
        color: $orange;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    position: relative;
    &__logo{
      width: 6.8125em;
    }
  }

  .header-top{
    padding: 0;
    border: none;
  }

  .nav{
    display: none;
  }

  .header-contacts{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 3em;
    .socials{
      &__item{
        @include lastM(0 .75em 0 0);
        img{
          margin-right: 0;
          width: 1.5em;
        }
        span{
          display: none;
        }
      }
    }
    &__email{
      display: none;
    }
    &__phone{
      width: 1.5em;
      @include center;
      margin-left: .75em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .header-bottom{
    padding: .875em 0;
  }

  .header-action{
    &__btn{
      display: none;
    }
  }

  .burger{
    width: 1.125em;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .header-contacts2{
    display: none;
  }

  .menu{
    &__top{
      padding: 0 .75em;
      height: 2.875em;
    }
    &__close{
      width: .8125em;
    }
    &__bottom{
      height: 9.75em;
      padding: 1.25em .75em;
    }
    &__scroll{
      height: calc(100 * var(--vh) - 12.625em);
      padding: 1.25em .75em;
    }
  }

  .menu-contacts{
    gap: .5em;
  }

  .menu-contacts2{
    margin-top: 1.06em;
    flex-direction: column;
    align-items: flex-start;
    &__email{
      order: -1;
      margin: 0 0 1em;
    }
  }

  .menu-nav{
    gap: 1.25em;
    &__icon{
      width: 1.5em;
      margin-right: .625em;
    }
    &__content{
      min-height: 1.5em;
    }
    &__title{
      font-size: .875em;
      line-height: 129%;
    }
    &__list{
      margin-top: .75em;
      a{
        font-size: .75em;
        line-height: 150%;
      }
    }
  }

  .menu-nav2{
    ul{
      gap: .75em;
    }
    a{
      font-size: .75em;
      line-height: 150%;
    }
  }
}
