@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 4em;
    border-radius: 0.375em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .9375em;
        line-height: 120%;
    }
    svg,
    img{
        margin-left: .75em;
        width: .6875em;
    }
    &._small{
        padding: 0 1.875em;
        height: 3.1875em;
    }
}

.m-btn-white-outline,
.m-btn-blue{

}

.m-btn-blue{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: $black;
        color: #fff;
    }
    &:disabled{
        background: rgba(88, 94, 102, 0.20);
    }
}

.m-btn-blue-outline{
    color: $primary;
    border: 2px solid $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
        path{
            stroke: #fff;
        }
    }
    &:disabled{
        border-color: rgba($gray, 0.20);
        color: rgba($gray, 0.20);
        path{
            stroke: rgba($gray, 0.20);
        }
    }
}

.m-btn-white-outline{
    border: 2px solid #fff;
    color: #fff;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $primary;
    &:hover{
        background-color: $black;
        color: #fff;
        path{
            stroke: #fff;
        }
    }
    &:disabled{
        background: rgba(88, 94, 102, 0.20);
        color: #fff;
        path{
            stroke: #fff;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.75em;
        padding: 0 1.875em;
        &._small{
            padding: 0 1.25em;
            height: 2.75em;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.9375em;
        svg,
        img{
            width: .5em;
            margin-left: .5em;
        }
        span{
            font-size: .875em;
        }
    }
}
