.offer{
  padding: 8.75em 0;
  background: linear-gradient(77deg, #F2F7FF 40.31%, #FDFEFF 99.53%);
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/flash.svg);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: right 5.38em top 0;
  }
  &__grid{
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 40.625em calc(100% - 40.625em);
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 3.125em;
  }
  &__img{
    position: relative;
    img{
      top: -7em;
      left: -10em;
      position: absolute;
      width: auto;
      height: 47.75em;
      display: block;
    }
  }
}

.offer__nav,
.you-get__items{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
  max-width: 33.5625em;
}

.you-get{
  margin-top: 2.5em;
  &__items{
    margin-top: 1em;
  }
}

.benefit-item{
  @include flex;
  border-radius: 0.875em;
  padding: 1.25em;
  background-color: #fff;
  &__icon{
    width: 2.75em;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.stage{
  padding: 8.75em 0;
  color: #fff;
  background-color: $black;
  .info-block{
    align-items: center;
  }
}

.stage-slider{
  margin-top: 3.125em;
  .swiper{
    &-container{
      width: 100%;
    }
    @media screen and (min-width: 577px){
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
        gap: 1.875em;
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.stage-card{
  padding: 2.5em;
  height: 100%;
  border-radius: 1.25em;
  background: #16171D;
  position: relative;
  &:hover{
    box-shadow: 0 1.875em 3.5em rgba(0, 82, 204, 0.25);
    &:before{
      opacity: 1;
    }
    .stage-card__icon{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .stage-card__desc,
    .stage-card__desc2{
      opacity: .8;
    }
    .stage-card__num{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='85' height='86' viewBox='0 0 85 86' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0H65C76.0457 0 85 8.95431 85 20V70H68.5C58.2599 70 45.489 70 34.993 70C23.9474 70 15 61.049 15 50.0033C15 39.8482 15 27.5637 15 17.5V0Z' fill='%23141C32'/%3E%3Cpath d='M0.5 0C9.5 0 15 6.5 15 17.5V0H0.5Z' fill='%23141C32'/%3E%3Cpath d='M68.5 70C79 70 85 76 85 85.5V70H68.5Z' fill='%23141C32'/%3E%3C/svg%3E");
    }
  }
  &:before{
    content: '';
    border-radius: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    opacity: 0;
    background: $grad;
    transition: inherit;
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding-left: 3.75em;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__icon{
    position: absolute;
    top: .375em;
    left: 0;
    width: 2.75em;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: block;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__title{
    padding-right: 2.5em;
  }
  &__desc{
    margin-top: 1em;
    opacity: .5;
  }
  &__info{
    margin-top: auto;
    padding-top: 1.25em;
    @include grid100gap(.125em);
  }
  &__desc2{
    opacity: .5;
  }
  &__num{
    width: calc(5.28125em + 2px);
    height: calc(5.34375em + 2px);
    position: absolute;
    top: -1px;
    right: -1px;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='85' height='86' viewBox='0 0 85 86' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0H65C76.0457 0 85 8.95431 85 20V70H68.5C58.2599 70 45.489 70 34.993 70C23.9474 70 15 61.049 15 50.0033C15 39.8482 15 27.5637 15 17.5V0Z' fill='%230F1015'/%3E%3Cpath d='M0.5 0C9.5 0 15 6.5 15 17.5V0H0.5Z' fill='%230F1015'/%3E%3Cpath d='M68.5 70C79 70 85 76 85 85.5V70H68.5Z' fill='%230F1015'/%3E%3C/svg%3E");
    @include bgImgSize(100%);
    z-index: 3;
    display: flex;
    justify-content: flex-end;
    padding: 1.37em;
  }
}


@media screen and (max-width: 992px){
  .offer{
    padding: 5em 0;
    background: $bgGray;
    &__grid{
      grid-template-columns: 100%;
    }
    &__desc{
      max-width: 33em;
    }
    &__nav{
      margin-top: 2.5em;
      @media screen and (min-width: 577px){
        display: flex;
        gap: unset;
        .m-btn{
          margin-right: 1.125em;
        }
      }
    }
    &:before,
    &__img{
      display: none;
    }
  }

  .you-get{
    margin-top: 1.875em;
    &__items{
      max-width: 100%;
    }
  }

  .stage{
    padding: 5em 0;
    .info-block{
      gap: 1.25em;
    }
    &__desc{
      max-width: 26.4375em;
    }
  }

  .stage-slider{
    margin-top: 2.5em;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .625em));
          gap: 1.25em;
        }
      }
    }
  }

  .stage-card{
    padding: 1.875em;
    &:hover{
      .stage-card__num{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='73' height='74' viewBox='0 0 73 74' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.9999 0H55.857C65.3247 0 72.9999 7.67512 72.9999 17.1429V60H58.857C50.9909 60 41.3825 60 32.9969 60C21.9512 60 12.9999 51.0504 12.9999 40.0047C12.9999 31.9217 12.9999 22.7021 12.9999 15V0Z' fill='%23141C32'/%3E%3Cpath d='M0.571289 0C8.28557 0 12.9999 5.57143 12.9999 15V0H0.571289Z' fill='%23141C32'/%3E%3Cpath d='M58.857 60C67.857 60 72.9999 65.1429 72.9999 73.2857V60H58.857Z' fill='%23141C32'/%3E%3C/svg%3E");
      }
    }
    &__inner{
      padding-left: 0;
    }
    &__icon{
      top: unset;
      position: relative;
      width: auto;
      height: 2.75em;
      margin-bottom: .75em;
      img{
        width: auto;
        height: 100%;
      }
    }
    &__title{
      padding-right: 0;
    }
    &__info{
      padding-top: 1em;
    }
    &__num{
      width: 4.52681em;
      height: 4.58038em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='73' height='74' viewBox='0 0 73 74' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.9999 0H55.857C65.3247 0 72.9999 7.67512 72.9999 17.1429V60H58.857C50.9909 60 41.3825 60 32.9969 60C21.9512 60 12.9999 51.0504 12.9999 40.0047C12.9999 31.9217 12.9999 22.7021 12.9999 15V0Z' fill='%230F1015'/%3E%3Cpath d='M0.571289 0C8.28557 0 12.9999 5.57143 12.9999 15V0H0.571289Z' fill='%230F1015'/%3E%3Cpath d='M58.857 60C67.857 60 72.9999 65.1429 72.9999 73.2857V60H58.857Z' fill='%230F1015'/%3E%3C/svg%3E");
      padding: 1.06em;
    }
    .hide-tablet{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .offer{
    padding: 3.75em 0;
    &__info{
      gap: 1em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__nav{
      margin-top: 1.875em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .you-get{
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .benefit-item{
    padding: 1em;
    &__text{
      span{
        display: none;
      }
    }
  }

  .stage{
    overflow: hidden;
    padding: 3.75em 0;
    .info-block{
      gap: 1em;
    }
  }

  .stage-slider{
    margin-top: 1.875em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 17.5em;
        padding-right: 1.25em;
      }
      &-pagination{
        &-bullet{
          &-active{
            background-color: #fff;
          }
        }
      }
    }
  }

  .stage-card{
    padding: 1.25em;
    &:hover{
      .stage-card__num{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='57' height='58' viewBox='0 0 57 58' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.99987 0H43.5713C50.9877 0 56.9999 6.01218 56.9999 13.4286V47H45.9213C39.5497 47 31.7188 47 24.9966 47C16.7123 47 9.99987 40.2845 9.99987 32.0002C9.99987 25.5137 9.99987 17.995 9.99987 11.75V0Z' fill='%23141C32'/%3E%3Cpath d='M0.26416 0C6.30702 0 9.99987 4.36428 9.99987 11.75V0H0.26416Z' fill='%23141C32'/%3E%3Cpath d='M45.9213 47C52.9713 47 56.9999 51.0286 56.9999 57.4071V47H45.9213Z' fill='%23141C32'/%3E%3C/svg%3E");
      }
    }
    &__info{
      padding-top: 1.25em;
    }
    &__num{
      width: 3.546em;
      height: 3.58794em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='57' height='58' viewBox='0 0 57 58' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.99987 0H43.5713C50.9877 0 56.9999 6.01218 56.9999 13.4286V47H45.9213C39.5497 47 31.7188 47 24.9966 47C16.7123 47 9.99987 40.2845 9.99987 32.0002C9.99987 25.5137 9.99987 17.995 9.99987 11.75V0Z' fill='%230F1015'/%3E%3Cpath d='M0.26416 0C6.30702 0 9.99987 4.36428 9.99987 11.75V0H0.26416Z' fill='%230F1015'/%3E%3Cpath d='M45.9213 47C52.9713 47 56.9999 51.0286 56.9999 57.4071V47H45.9213Z' fill='%230F1015'/%3E%3C/svg%3E");
      padding: .875em;
    }
  }
}
