.cases{
  padding: 4.375em 0 8.75em;
  &__top,
  &__nav{
    display: grid;
    grid-template-columns: 47.75em 26.4375em;
    justify-content: space-between;
    align-items: flex-end;
    gap: 1.875em;
  }
  &__nav{
    gap: 0;
    margin-top: 2.5em;
  }
  &__btn{
    display: inline-flex;
    margin-right: auto;
  }
}

.tabs{
  display: flex;
}

.tab{
  @include lastM(0 2.5em 0 0);
  font-weight: 500;
  opacity: .3;
  transition: all .3s;
  height: 2.5em;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
  display: flex;
  align-items: center;
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &:hover{
    cursor: pointer;
    opacity: 1;
  }
  &._active{
    border-bottom-color: $primary;
    opacity: 1;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.cases-collection{
  margin-top: 3.125em;
}

.m-badge{
  padding: 0 1.25em;
  height: 2.375em;
  display: inline-flex;
  align-items: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 56.25em;
    background: rgba(255, 255, 255, 0.10);
    backdrop-filter: blur(7.5px);
  }
  span{
    position: relative;
    z-index: 2;
  }
  img{
    width: 1.125em;
    margin-right: .5em;
  }
  &._gray{
    color: $gray;
    &:before{
      background: $bgGray;
      backdrop-filter: unset;
    }
  }
  &._gray-outline{
    color: $gray;
    &:before{
      background: transparent;
      border: $border;
      backdrop-filter: unset;
    }
  }
}

.case-card{
  padding: 3.75em;
  min-height: 34.875em;
  position: relative;
  grid-column: 1/-1;
  color: #fff;
  @media screen and (min-width: 577px){
    &._green{
      .case-card__btn{
        &:not(:hover){
          color: #177866;
          path{
            stroke: #177866;
          }
        }
      }
    }
    &._dark{
      .case-card__btn{
        &:not(:hover){
          color: #363645;
          path{
            stroke: #363645;
          }
        }
      }
    }
    &._brown{
      .case-card__btn{
        &:not(:hover){
          color: #635A53;
          path{
            stroke: #635A53;
          }
        }
      }
    }
    &._blue{
      .case-card__btn{
        &:not(:hover){
          color: #6F91E5;
          path{
            stroke: #6F91E5;
          }
        }
      }
    }
    &._orange{
      .case-card__btn{
        &:not(:hover){
          color: #FF710A;
          path{
            stroke: #FF710A;
          }
        }
      }
    }
  }
  @media screen and (min-width: 993px){
    &._small{
      grid-column: unset;
      min-height: 27.875em;
      .case-props{
        grid-template-columns: 100%;
        gap: 1em;
        &__item{
          padding: 0;
          border: none !important;
        }
      }
      .case-card{
        &__img{
          z-index: -1;
        }
        &__content{
          max-width: 23.875em;
          position: static;
        }
        &__btn{
          border-radius: 50%;
          width: 4.375em;
          height: 4.375em;
          padding: 0;
          position: absolute;
          right: 3.75em;
          bottom: 3.75em;
          svg{
            margin: 0;
          }
          span{
            display: none;
          }
        }
      }
    }
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.25em;
    img{
      border-radius: inherit;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    width: 35.125em;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__badge{
    margin-bottom: .75em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
}

.case-props{
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: 1.875em;
  width: 100%;
  &__item{
    display: flex;
    align-items: flex-start;
    @media screen and (min-width: 993px){
      padding: 1em 1.25em;
      border-right: 1px solid rgba(255, 255, 255, 0.20);
      border-top: 1px solid rgba(255, 255, 255, 0.20);
      &:nth-last-child(1),
      &:nth-last-child(2){
        border-bottom: 1px solid rgba(255, 255, 255, 0.20);
      }
      &:nth-child(odd){
        border-left: 1px solid rgba(255, 255, 255, 0.20);
      }
      &:nth-child(1){
        border-top-left-radius: .875em;
      }
      &:nth-child(2){
        border-top-right-radius: .875em;
      }
      &:nth-last-child(1){
        border-bottom-right-radius: .875em;
      }
      &:nth-last-child(2){
        border-bottom-left-radius: .875em;
      }
    }
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    @include grid100gap(.125em);
  }
  &__title{
    opacity: 0.6;
  }
  &__desc{
    font-weight: 400;
    a{
      border-bottom: 1px solid rgba(255, 255, 255, 0.20);
      &:hover{
        color: #fff;
        border-bottom-color: #fff;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .cases{
    padding: 2.5em 0 5em;
    &__inner{
      position: relative;
      padding-bottom: 5.625em;
    }
    &__top,
    &__nav{
      grid-template-columns: 100%;
    }
    &__desc{
      max-width: 29.4375em;
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__btn{
      white-space: nowrap;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .tab{
    @include lastM(0 1.875em 0 0);
    height: 2.25em;
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .cases-collection{
    margin-top: 2.5em;
  }

  .m-badge{
    height: 2.25em;
  }

  .case-card{
    padding: 2.5em;
    min-height: 28.125em;
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    &__content{
      width: 22em;
    }
    &__title{
      font-weight: 500;
    }
  }

  .case-props{
    grid-template-columns: 100%;
    gap: 1em;
    ._hide-tablet{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .cases{
    padding: 1.875em 0 3.75em;
    &__inner{
      padding-bottom: 4.8125em;
    }
    &__top{
      gap: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .tabs{
    overflow-x: auto;
    padding-bottom: .5em;
    margin-bottom: -.5em;
    @include fullScreenMob;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  .tab{
    @include lastM(0 1.25em 0 0);
    height: 2.125em;
    flex-shrink: 0;
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }

  .cases-collection{
    margin-top: 1.875em;
  }

  .m-badge{
    height: 1.75em;
    padding: 0 .75em;
  }

  .case-card{
    padding: 0;
    min-height: unset;
    &__img{
      position: static;
      height: 14.375em;
      margin-bottom: .75em;
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    &__content{
      width: 100%;
      height: unset;
      position: static;
    }
    &__badge{
      position: absolute;
      top: 1.25em;
      left: 1.25em;
      margin: 0;
    }
    &__title{
      color: $black;
      font-weight: 400;
    }
    &__nav{
      position: absolute;
      right: 1.25em;
      top: 10.19em;
      padding: 0;
      margin: 0;
    }
    &__btn{
      width: 2.9375em;
      height: 2.9375em;
      padding: 0;
      border-radius: 50%;
      box-shadow: 0 1em .875em rgba(73, 93, 131, 0.20);
      svg{
        margin: 0;
      }
      span{
        display: none;
      }
      path{
        stroke: $black;
      }
    }
  }

  .case-props{
    display: none;
  }
}
