.partners{
  padding: 8.75em 0;
  overflow: hidden;
  &__groups{
    @include grid100gap(1.25em);
  }
  &__group{
    display: flex;
    &:nth-child(even){
      flex-direction: row-reverse;
      .partners__items{
        animation-name: slides2;
      }
    }
    &:hover{
      .partners__items{
        animation-play-state: paused;
      }
    }
  }
  &__items{
    display: flex;
    animation: 25s linear infinite slides;
  }
  .client-card{
    width: 12.3125em;
    height: 4.75em;
    flex-shrink: 0;
    margin-right: 1.25em;
    &:not(:hover) img{
      opacity: 0.5;
      mix-blend-mode: luminosity;
      filter: grayscale(100%);
    }
    img{
      transition: all .3s;
    }
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

@keyframes slides2 {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}

.crm{
  padding: 8.75em 0;
  border-top: $border;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 39.75em) 33.5em;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  &__items{
    margin-top: 2.5em;
  }
  &__img{
    position: relative;
    img{
      top: -5em;
      left: -2em;
      width: 48.9375em;
      position: absolute;
    }
  }
}

.equip-item{
  border-radius: 1em;
  border: $border;
  background-color: #fff;
  @include center;
  height: 6.4375em;
  padding: 1.875em;
  img{
    max-width: 100%;
    max-height: 100%;
  }
}

.marketing{
  padding: 8.75em 0;
  background-color: $bgGray;
  background-image: url(../img/marketing-bg.svg);
  background-repeat: no-repeat;
  background-size: 51.375em auto;
  background-position: right 13.67em top .75em;
  &__top{
    display: grid;
    grid-template-columns: 37.6875em calc(100% - 37.6875em - 6.19em);
    justify-content: space-between;
    align-items: center;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  &__desc{
    max-width: 33.5em;
  }
  &__img{
    width: calc(100% + 1em);
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(-1.25em -3.75em 6.25em rgba(99, 122, 165, 0.11));
    }
  }
  &__items{
    margin-top: 6.25em;
    align-items: flex-start;
  }
}

.marketing-item{
  @include grid100gap(.75em);
}

.seo{
  padding: 8.125em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: 39.25em 40.625em;
    gap: 3.25em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: 3.875em 0;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
    margin-top: 1.875em;
  }
}

.seo-item{
  border-radius: 0.75em;
  border: $border;
  padding: 1.25em 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    height: 2.6875em;
    margin-bottom: 1em;
    img{
      height: 100%;
      display: block;
      width: auto;
    }
  }
}

.cms{
  padding: 8.75em 0;
  background-color: $bgGray;
  background-image: url(../img/cms-bg.svg);
  background-repeat: no-repeat;
  background-position: right 11.69em top 3.875em;
  background-size: 53.9375em auto;
  &__inner{
    display: grid;
    grid-template-columns: 40.375em 30em;
    gap: 7.69em;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  .equip-item{
    border-radius: .5em;
    height: 6.125em;
  }
}


@media screen and (max-width: 992px){
  .partners{
    padding: 5em 0;
  }

  .crm{
    padding: 5em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__info{
      gap: 1.25em;
      max-width: 31em;
    }
    &__items{
      margin-top: 1.875em;
    }
  }

  .marketing{
    padding: 5em 0;
    overflow: hidden;
    background-image: unset;
    &__top{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__info{
      gap: 1.25em;
      max-width: 36.75em;
    }
    &__desc{
      max-width: 100%;
    }
    &__items{
      margin-top: 5em;
    }
  }

  .seo{
    padding: 5em 0 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__content{
      padding: 0;
      order: -1;
      .h2{
        max-width: 14em;
      }
    }
    &__info{
      max-width: 35.125em;
      gap: 1.25em;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .seo-item{
    &__img{
      height: 3.75em;
    }
  }

  .cms{
    padding: 5em 0;
    background-image: url(../img/cms-bg2.svg);
    background-position: right 0 top 15.95em;
    background-size: 100% auto;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__info{
      gap: 1.25em;
    }
    .equip-item{
      height: 5.125em;
      padding: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners{
    padding: 3.75em 0;
    &__groups{
      gap: 1em;
    }
    .client-card{
      width: 6.25em;
      height: 2.5em;
      margin-right: 1em;
      padding: .5em;
    }
  }

  .crm{
    padding: 3.75em 0;
    &__info{
      gap: 1em;
    }
  }

  .equip-item{
    height: 4.75em;
    padding: 1.5em 1.17em;
  }

  .marketing{
    padding: 3.75em 0;
    &__info{
      gap: 1em;
    }
    &__img{
      width: 100%;
    }
    &__items{
      margin-top: 3.75em;
    }
  }

  .seo{
    padding: 3.75em 0 0;
    &__inner{
      gap: 1.875em;
    }
    &__info{
      gap: 1em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .seo-item{
    padding: 1em;
    &__img{
      height: 2.9375em;
      margin-bottom: .75em;
    }
  }

  .cms{
    padding: 3.75em 0;
    background-image: url(../img/cms-bg3.svg);
    background-position: right 0 top 13.28em;
    &__inner{
      gap: 1.875em;
    }
    &__info{
      gap: 1em;
    }
    .equip-item{
      height: 4.75em;
    }
  }
}
