.service-item{
  padding: 10em 0 6.25em;
  @media screen and (min-width: 993px){
    .info-block{
      grid-template-columns: 45em 26.4375em;
      align-items: flex-start;
      .p{
        padding-top: 1.81em;
      }
    }
  }
  &__grid{
    margin-top: 10em;
  }
}

.service-card2{
  padding: 1.25em;
  &:before{
    background-color: $primary;
    background-image: url(../img/service-bg.svg);
    background-size: 19.375em;
    background-repeat: no-repeat;
    background-position: left bottom;
  }
  .service-card__info{
    margin-top: 0;
    gap: .75em;
  }
  .service-card__bottom{
    padding-top: 1.125em;
  }
}

.tariffs{
  padding: 8.75em 0 6.25em;
  background-color: $black;
  color: #fff;
  &__grid{
    margin-top: 3.125em;
  }
}

.tariff-card{
  border-radius: 1.25em;
  background: #16171D;
  padding: 2.5em;
  display: flex;
  flex-direction: column;
  &._active{
    background: linear-gradient(164deg, #0052CC 5.49%, #0F93F2 89.44%), #16171D;
    .gray-text2{
      color: rgba(255,255,255,.8);
    }
    .tariff-card__top{
      &:after{
        content: '';
        flex-shrink: 0;
        margin-left: 1em;
        background-image: url(../img/premium.svg);
        @include bgImgSize(100%);
        width: 2.5em;
        height: 2.5em;
      }
    }
    .tariff-card__btn{
      background-color: #fff;
      color: $primary;
      path{
        stroke: $primary;
      }
      &:hover{
        background-color: $black;
        color: #fff;
        path{
          stroke: #fff;
        }
      }
    }
  }
  &__top{
    @include flex;
    justify-content: space-between;
  }
  &__info{
    margin-top: .75em;
    @include grid100gap(1.25em);
  }
  &__info2{
    margin-top: 1.875em;
    @include grid100gap(.125em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
  }
  &__btn{
    margin-top: 1.25em;
    width: 100%;
  }
}

.advantages2{
  margin-top: 6.25em;
}

.advantage-item{
  @include flex;
  padding: 1.25em;
  min-height: 4em;
  border-radius: 0.75em;
  border: 1px solid rgba(255, 255, 255, 0.10);
  img{
    width: 1.5em;
    margin-right: .75em;
  }
}

.integration{
  padding: 4.75em 0 0;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 33.5625em calc(100% - 33.5625em - 6.25em);
    justify-content: space-between;
    align-items: flex-start;
  }
  &__content{
    padding: 4em 0;
    @include grid100gap(1.875em);
  }
  &__img{
    position: relative;
    img{
      width: 53.75em;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1700px){
  .integration{
    &__img{
      img{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .service-item{
    padding: 5em 0;
    .info-block{
      max-width: 36.875em;
    }
    &__grid{
      margin-top: 5em;
    }
  }

  .service-card2{
    .service-card__bottom{
      padding-top: 1.25em;
    }
  }

  .tariffs{
    padding: 5em 0;
    &__grid{
      margin-top: 2.5em;
    }
  }

  .tariff-card{
    padding: 1.875em;
    &._active{
      .tariff-card__top{
        &:after{
          width: 2.25em;
          height: 2.25em;
        }
      }
    }
    &__info{
      gap: 1em;
    }
    &__info2{
      margin-top: 1.25em;
    }
    &__bottom{
      padding-top: 1.875em;
    }
  }

  .advantages2{
    margin-top: 5em;
  }

  .integration{
    padding: 5em 0 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0;
      gap: 1.25em;
      max-width: 35em;
    }
    &__img{
      width: calc(100% + 3.5em);
      margin-left: -1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .service-item{
    padding: 3.75em 0;
    &__grid{
      margin-top: 3.75em;
    }
  }

  .service-card2{
    .service-card__top{
      padding-left: 0;
      height: unset;
    }
  }

  .tariffs{
    padding: 3.75em 0;
    &__grid{
      margin-top: 1.875em;
    }
  }

  .tariff-card{
    padding: 1.25em;
    &._active{
      .tariff-card__top{
        &:after{
          width: 1.75em;
          height: 1.75em;
        }
      }
    }
    &__info{
      gap: 1em;
    }
    &__bottom{
      padding-top: 1.25em;
    }
  }

  .advantages2{
    margin-top: 3.75em;
  }

  .integration{
    padding: 3.75em 0 1em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      gap: 1em;
    }
    &__img{
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
