.breadcrumbs {
    padding: 2.5em 0 1.875em;
    position: relative;
    z-index: 2;
    &__item {
        position: relative;
        display: inline;
        &:last-child {
            color: $gray;
        }
        &:not(:first-child) {
            margin-left: 1.75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                color: $gray;
                top: .125em;
                right: 100%;
                margin-right: .75em;
                font-size: .875em;
                line-height: 129%;
                opacity: 0.2;
            }
        }
        span{
            font-size: .875em;
            line-height: 129%;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1.25em 0;
        &__items{
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            max-width: 26em;
        }
        &__item {
            &:not(:first-child) {
                margin-left: 1.5em;
                &:before {
                    top: .3125em;
                    margin-right: .75em;
                    font-size: .75em;
                    line-height: 133%;
                }
            }
            span{
                font-size: .75em;
                line-height: 133%;
            }
        }
    }
}



@media screen and (max-width: 576px){
    .breadcrumbs{
        &__items{
            max-width: 100%;
        }
        &__item {
            &:not(:first-child) {
                margin-left: 1em;
                &:before {
                    margin-right: .5em;
                }
            }
        }
    }
}
