.contacts-info{
  padding-top: 3.125em;
  margin-top: 6.25em;
  border-top: 1px solid $bgGray;
}

.contacts-item{
  &__bottom{
    margin-top: .75em;
  }
  &__email{
    color: $primary;
    &:hover{
      color: $orange;
    }
  }
}

.socials{
  @include flex;
  &__item{
    @include flex;
    font-weight: 500;
    color: $primary;
    @include lastM(0 1.25em 0 0);
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    &:hover{
      color: $orange;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    .info-block{
      max-width: 32em;
      .p{
        max-width: 35em;
      }
    }
  }

  .contacts-info{
    padding-top: 2.5em;
    margin-top: 3.75em;
    grid-template-columns: 12em calc(100% - 13.125em);
    gap: 1.875em 1.125em;
  }

  .contacts-item{
    &__bottom{
      margin-top: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-page{
    .info-block{
      gap: 1em;
    }
  }

  .contacts-info{
    padding-top: 1.25em;
    margin-top: 1.875em;
    grid-template-columns: 100%;
    gap: 1em;
  }

  .contacts-item{
    &__bottom{
      margin-top: .375em;
    }
  }

  .socials{
    &__item{
      @include lastM(0 1em 0 0);
    }
  }
}
