.about,
.vacancies-page{
  background-image: url(../img/vacancies-bg.svg);
  margin-top: -5.625em;
  padding-top: 5.625em;
}

.vacancies-page{
  .tabs{
    margin-top: 3.125em;
  }
  &__grid{
    margin-top: 3.125em;
  }
  &__content{
    grid-column: span 2;
  }
  &__items{
    @include grid100gap(2.5em);
  }
  &__grid2{
    margin-top: 2.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
}

.vacancy-sidebar{
  padding: 2.5em;
  border-radius: 1.25em;
  background-color: $bgGray;
  position: sticky;
  top: 1em;
  &__icon{
    border-radius: 1.25em;
    width: 4.375em;
    height: 4.375em;
    background-color: #fff;
    @include center;
    margin-bottom: 1.875em;
    img{
      width: 1.9375em;
    }
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__bottom{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
}

.vacancy-card{
  @include grid100gap(1em);
  &__bottom{
    @include flex;
    justify-content: space-between;
  }
  &__badges{
    @include flex;
    .m-badge{
      margin-right: .75em;
    }
  }
  .m-link{
    svg{
      width: .6875em;
      margin-left: .75em;
      margin-bottom: -.1em;
    }
    &:hover{
      path{
        stroke: $orange;
      }
    }
  }
}

.vacancy-text{
  margin-top: 2.5em;
  h4{
    padding-left: 1em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/flash2.svg);
      @include bgImgSize(100%);
      width: .57em;
      height: 1.21em;
      left: 0;
      top: 0;
    }
  }
  p{
    @include lastM(0 0 1em);
  }
  hr{
    height: 1em;
  }
}


@media screen and (max-width: 992px){
  .about,
  .vacancies-page{
    background-image: url(../img/vacancies-bg2.svg);
    margin-top: -3.625em;
    padding-top: 3.625em;
  }

  .vacancies-page{
    .tabs{
      margin-top: 1.875em;
    }
    &__grid,
    &__grid2{
      margin-top: 2.5em;
      gap: 2.5em;
    }
    &__content{
      grid-column: unset;
    }
    &__items{
      gap: 1.875em;
    }
    &__nav{
      margin-top: 1.875em;
    }
  }

  .vacancy-sidebar{
    padding: 1.875em;
    position: static;
    &__icon{
      width: 3.75em;
      height: 3.75em;
      margin-bottom: 1.25em;
      img{
        width: 1.6875em;
      }
    }
    &__info{
      gap: 1em;
    }
    @media screen and (min-width: 577px){
      &__bottom{
        margin-top: 1.875em;
        gap: unset;
        display: flex;
        align-items: center;
      }
      &__btn{
        flex-grow: 1;
        margin-right: 1.875em;
      }
    }
  }

  .vacancy-text{
    h4{
      padding-left: 1.17em;
      &:before{
        width: .67em;
        height: 1.17em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about,
  .vacancies-page{
    background-image: url(../img/vacancies-bg3.svg);
  }

  .vacancies-page{
    .tabs{
      margin-top: 1.25em;
    }
    &__grid{
      margin-top: 1.875em;
    }
    &__grid2{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .vacancy-sidebar{
    padding: 1.25em;
    &__icon{
      width: 2.9375em;
      height: 2.9375em;
      margin-bottom: 1em;
      border-radius: .625em;
      img{
        width: 1.3125em;
      }
    }
    &__info{
      gap: .75em;
    }
    &__bottom{
      margin-top: 1.25em;
      gap: 1.25em;
    }
    .socials{
      justify-content: center;
    }
  }

  .vacancy-card{
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    .m-link{
      margin-top: 1em;
    }
  }

  .vacancy-text{
    h4{
      padding-left: 1.55em;
      &:before{
        width: .89em;
        height: 1.22em;
      }
    }
  }
}
