.main{
  padding: 5em 0 4.375em;
  position: relative;
  &__bg{
    pointer-events: none;
    z-index: -1;
    position: absolute;
    top: -1em;
    right: 0;
    width: 101.5625em;
    &._view2{
      display: none;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 26.4375em - 1.875em) 26.4375em;
    gap: 1.875em;
  }
  &__desc{
    padding-top: 1.44em;
  }
}

.main-services{
  margin-top: 10em;
}

.service-card{
  padding: 2.5em;
  background: $bgGray;
  border-radius: 1.25em;
  position: relative;
  &:hover{
    color: #fff;
    .gray-text{
      color: rgba(#fff,.8);
    }
    &:before{
      opacity: 1;
    }
    .service-card__icon{
      background-color: transparent;
      &:before{
        opacity: 1;
      }
    }
    ._black{
      stroke: #fff;
    }
    ._blue{
      stroke: $blueLite;
    }
    ._blue-fill{
      fill: $blueLite;
    }
    ._white{
      fill: transparent;
      opacity: 0;
    }
    .service-card__img{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    transition: inherit;
    background: $grad;
    opacity: 0;
  }
  &__inner{
    position: relative;
    z-index: 2;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  &__icon{
    width: 5.625em;
    height: 5.625em;
    background-color: #fff;
    border-radius: 1.125em;
    @include center;
    position: relative;
    transition: all .125s;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      transition: inherit;
      background: linear-gradient(167deg, rgba(255, 255, 255, 0.23) -45.59%, rgba(255, 255, 255, 0.00) 115.3%);
      opacity: 0;
    }
    svg{
      width: 2.75em;
      position: relative;
      z-index: 2;
    }
  }
  &__info{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
  &__info2{
    margin-top: 1.875em;
    @include grid100gap(.125em);
  }
  &__bottom{
    padding-top: 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
  }
  &__btn{
    width: 4.375em;
    height: 4.375em;
    background-color: #fff;
    border-radius: 50%;
    @include center;
    img{
      width: .6875em;
    }
  }
  &__img{
    position: absolute;
    top: 6.69em;
    right: 0;
    width: 11.0625em;
    opacity: 0;
    pointer-events: none;
    transition: all .125s;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 5em 0 2.5em;
    overflow: hidden;
    &__bg{
      top: 2.75em;
      right: -12em;
      height: 46em;
      width: auto;
    }
    &__inner{
      max-width: 41em;
      grid-template-columns: 100%;
    }
    &__desc{
      max-width: 26.4375em;
      padding: 0;
    }
  }

  .main-services{
    margin-top: 5em;
  }

  .service-card{
    padding: 1.875em;
    &__icon{
      width: 4.375em;
      height: 4.375em;
      border-radius: .875em;
      svg{
        width: 2.25em;
      }
    }
    &__info{
      margin-top: 1.25em;
      gap: 1.25em;
    }
    &__info2{
      margin-top: 1.25em;
    }
    &__btn{
      width: 3.75em;
      height: 3.75em;
    }
    &__img{
      top: 5.5em;
      width: 9em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 3.75em 0 1.875em;
    &__bg{
      top: .69em;
      right: 0;
      height: auto;
      width: 100%;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__inner{
      gap: 1.25em;
    }
    .h1{
      max-width: 82%;
    }
  }

  .main-services{
    margin-top: 3.75em;
  }

  .service-card{
    padding: 1.25em;
    &__top{
      height: 2.9375em;
      display: flex;
      align-items: center;
      padding-left: 3.9375em;
    }
    &__icon{
      width: 2.9375em;
      height: 2.9375em;
      border-radius: .5875em;
      position: absolute;
      top: 0;
      left: 0;
      svg{
        width: 1.8125em;
      }
    }
    &__info{
      margin-top: 0;
      gap: 1em;
    }
    &__info2{
      margin-top: 1em;
    }
    &__btn{
      width: 2.9375em;
      height: 2.9375em;
      img{
        width: .5625em;
      }
    }
    &__img{
      top: 4.5em;
      width: 8em;
    }
  }
}
